import { h, Component } from 'preact'
import { Router } from 'preact-router'

import Navbar from '../components/navbar'
import Home from '../routes/home'
import Demographics from '../routes/demographics'

if (module.hot)
	require('preact/debug')

export default class App extends Component {
	
    state = {
        showBack: false
    }
    
    homePath = process.env.GH_PAGES ? '/zip-code-please/' : '/'
    demoPath = process.env.GH_PAGES ? '/zip-code-please/demographics/' : '/demographics/'
    
    /** Gets fired when the route changes.
	 *	@param {Object} event		"change" event from [preact-router](http://git.io/preact-router)
	 *	@param {string} event.url	The newly routed URL
	 */
	handleRoute = async (e) => {
        this.setState({ showBack: (e.url !== this.homePath) })
	}
    
	render(props, state) {
        
		return (
            
            <div id="app">
                <Navbar showBack={state.showBack} />
                <div id="page">
    				<Router onChange={this.handleRoute}>
    					<Home path={this.homePath} />
                        <Demographics path={`${this.demoPath}:lat?/:lon?`} />
    				</Router>
                </div>
			</div>
            
		)
        
	}
    
}



